import axios from 'axios';
import Vue from 'vue';
import router from './router.js'// {currentRoute,push,replace} -> currentRoute=={key:value,fullPath}
import store from './vuex.js'
//
axios.interceptors.request.use(function(config) {
  console.log('');
  //config   axios(config)
  
  //    token
 let token = store.state.user.user.token;
 token = token ? token : '';
 config.headers={
   token
 }
 
  //loading
  // vm.bLoading=true;
  store.commit('UPDATE_LOADING',true)
  return config;// 
}, function(error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
  console.log('',response.data.err);
  //response  ~~ axiosres
  
  //loading
  // vm.bLoading=false;
  store.commit('UPDATE_LOADING',false)
  
  
  //    res.status   res.data.err == 2
  
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//string
  if(response.data.err===2 && !currentRoute.includes('/login')){
    router.replace({
      path:'/login',
      query:{p:currentRoute}
    })
  }
  
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
window.axios=axios;
export default axios;